#oche_report {
  width: 100%;
  display: flex !important;
  flex-direction: row;
  height: 100%;
  overflow: hidden;
}

.summary-list {
  width: 50%;
  min-width: 50%;
  height: 100%;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.summary-list .oche-inputs-item,
.summary-list .s-action-main {
  border: none;
  margin-top: 1rem;
}

.single-section {
  border: none;
}

.single-row {
  width: 100%;
  background-color: white;
  padding: 1rem;
}

.r-space-table {
  height: auto;
  width: auto;
}

th,
td {
  border-bottom: 1px solid #ddd;
  padding: 0.5rem 1rem;
  font-size: small;
  text-align: left;
  max-width: 30rem;
  min-width: 30rem;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}

.min-width-th, .min-width-th-n{
  width: 5rem !important;
  max-width: 5rem !important;
  min-width: 5rem !important;
}

.min-width-th-n{
  width: 3rem !important;
  max-width: 3rem !important;
  min-width: 3rem !important;
}

th {
  background-color: #04AA6D;
  color: white;
}

.r-space{
  height: 100%;
  padding: 1rem 0rem;
  display: flex;
  flex-direction: column;
  overflow-x: auto;
  overflow-y: auto;
}

.r-space-actions{
  display: flex;
  flex-direction: row;
  width: 30rem;
  column-gap: 1rem;
}


.r-space-actions button, .r-space button{
  white-space: nowrap;
  font-size: small !important;
  padding: 0rem 1rem !important;
  background-color: white;
  color: #04AA6D;
}

.r-space .input-labeled label{
  font-size: small !important;
}

.r-space .oche-inputs-title .p-medium{
  font-size: small;
}

.r-space-cell-values-data{
  display: flex;
  flex-direction: row;
  column-gap: .5rem;
  align-items: flex-start;
  width: 100%;
}

.r-space-cell-values-data-form{
  width: 100%;
}

.r-space-cell-values-data .input-labeled{
  margin-top: 0;
}