.modal-curtain {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1300;
  background-color: rgba(0, 0, 0, 0.5);
}

.oche-mini-modal {
  width: 80%;
  height: 80%;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  overflow-y: hidden;
  background-color: var(--card);
  padding: 1rem;
  align-items: flex-end;
}

.oche-modal i {
  font-size: x-larger;
  text-align: end;
  cursor: pointer;
}

.modal-child {
  width: 100%;
  height: 100%;
  min-height: 100%;
  min-width: 100%;
  overflow: hidden;
}

.mini-modal-header{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}
